module.exports = function(gantt) {

	gantt.getTaskType = function (type) {
		var checkType = type;
		if(type && typeof type == "object"){
			checkType = type.type;
		}

		for (var i in this.config.types) {
			if (this.config.types[i] == checkType) {
				return checkType;
			}
		}
		return gantt.config.types.task;
	};

	gantt.form_blocks.typeselect = {
		render: function (sns) {
			var types = gantt.config.types,
				locale = gantt.locale.labels,
				options = [];

			var filter = sns.filter || function () {
				return true;
			};
			for (var i in types) {
				if (!filter(i, types[i]) === false) {
					options.push({key: types[i], label: locale["type_" + i]});
				}
			}
			sns.options = options;

			var oldOnChange = sns.onchange;
			sns.onchange = function () {
				var tId = gantt.getState().lightbox;

				gantt.changeLightboxType(this.value);
				if (typeof oldOnChange == 'function') {
					oldOnChange.apply(this, arguments);
				}
			};

			return gantt.form_blocks.select.render.apply(gantt, arguments);
		},
		set_value: function () {
			return gantt.form_blocks.select.set_value.apply(gantt, arguments);
		},
		get_value: function () {
			return gantt.form_blocks.select.get_value.apply(gantt, arguments);
		},
		focus: function () {
			return gantt.form_blocks.select.focus.apply(this, arguments);
		}
	};

};


